import { providers, Contract } from 'ethers';
import { ABI_BEP20 } from '../config/abiBep20Config';
import { ABI_BEP20_PROXY } from '../config/abiBep20ProxyConfig';
import { BSC_NODE, BEP20_PROXY } from '../config/web3Config';

export const bscProvider = new providers.JsonRpcProvider(BSC_NODE);

export const bep20Proxy: Contract = new Contract(
  BEP20_PROXY,
  ABI_BEP20_PROXY,
  bscProvider
);

export function getBep20Contract(address: string): Contract {
  return new Contract(address, ABI_BEP20, bscProvider);
}
